@import 'variables';

.main {
  margin: 10px 10px 0 10px;
}

h2 {
  border-bottom: 1px solid $appColour;
}

footer {
  background-color: #f2f2f2;
  bottom: 0;
  height: $footerHeight;
  text-align: center;
  padding-top: 4px;
  position: absolute;
  width: 100%;
}

header,
.assumptions {
  margin: 0 auto;
  max-width: 800px;
}

.assumptions {
  margin-top: 10px;
}
